<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
// import { ValidationProvider, ValidationObserver } from 'vee-validate'
// import { required } from '@validations'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BFormTextarea,
  BFormRadioGroup,
  BForm,
  BButton,
  //   BFormCheckboxGroup,
  BCard,
  BLink,
  // BInputGroupAppend,
  // BInputGroup,

} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import { heightTransition } from '@core/mixins/ui/transition'
import Ripple from 'vue-ripple-directive'

export default {
  components: {
  //  Ripple,
    BFormRadioGroup,
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BForm,
    BButton,
    BLink,
    BFormGroup,
    BFormInput,
    BFormTextarea,

    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
    VuexyLogo,
    BCard,
  },
  directives: {
    Ripple,
  },
  mixins: [heightTransition],
  /*  props: {
    startIndex: {
      type: Number,
      default: 0,
    },
  }, */
  data() {
    return {
      actividad_id: this.$attrs.id,
      evidenciaNueva: {
        nombre: '',
      },
      pregunta1: 'Administrativo',
      pregunta2: 'Si',
      pregunta3: {},
      pregunta4: {},
      options1: [
        { text: 'Estudiante', value: 'Estudiante' },
        { text: 'Personal Docente', value: 'Docente' },
        { text: 'Personal no docente', value: 'no docente' },
        { text: 'Egresado', value: 'Egresado' },
        { text: 'Tercero', value: 'Tercero' },
      ],
      options2: [
        { text: 'Si', value: 'Si' },
        { text: 'No', value: 'No' },
      ],
      options3: [
        { text: 'Estudiante', value: 'Estudiante' },
        { text: 'Personal Docente', value: 'Docente' },
        { text: 'Personal no docente', value: 'no docente' },
        { text: 'Egresado', value: 'Egresado' },
      ],
      items: [{
        id: 1,
        selected: 'male',
        selected1: 'designer',
        prevHeight: 0,
      }],
      items2: [{
        id: 1,
        selected: 'male',
        selected1: 'designer',
        prevHeight: 0,
      }],
      nextTodoId: 2,
      nextTodoId2: 2,
    }
  },
  computed: {

    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => ({ text: f.label, value: f.key }))
    },
  },
  mounted() {
    this.initTrHeight()
    this.initTrHeight2()
  },
  created() {
    window.addEventListener('resize', this.initTrHeight)
    window.addEventListener('resize', this.initTrHeight2)
  },
  destroyed() {
    window.removeEventListener('resize', this.initTrHeight)
    window.removeEventListener('resize', this.initTrHeight2)
  },
  methods: {
    eliminarEvidencia(id) {
      console.log(id)
      /*  store.dispatch('actividades_depor/deleteEvidencia', id).then(res => {
        console.log('respuesta: ', res)
        this.swalSuccess(`Evidencia ${res.data.data.nombre} eliminada`)
        store.dispatch('actividades_depor/fetchEvidencias', this.actividad_id)
      }) */
    },
    validationFormInfo() {
      return new Promise((resolve, reject) => {
        this.$refs.infoRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    toast(text, tipo, titulo) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: titulo,
          icon: 'BellIcon',
          text,
          variant: tipo,
        },
      })
    },
    formSubmitted() {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: 'Form Submitted',
          icon: 'EditIcon',
          variant: 'success',
        },
      })
    },
    swalSuccess(texto) {
      this.$swal({
        position: 'top-end',
        icon: 'success',
        title: texto,
        showConfirmButton: false,
        timer: 1500,
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },
    repeateAgain() {
      this.items.push({
        id: this.nextTodoId += this.nextTodoId,
      })

      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row[0].offsetHeight)
      })
    },
    repeateAgain2() {
      this.items2.push({
        id: this.nextTodoId2 += this.nextTodoId2,
      })

      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row2[0].offsetHeight)
      })
    },
    removeItem(index) {
      this.items.splice(index, 1)
      this.trTrimHeight(this.$refs.row[0].offsetHeight)
    },
    removeItem2(index) {
      this.items2.splice(index, 1)
      this.trTrimHeight(this.$refs.row2[0].offsetHeight)
    },
    initTrHeight() {
      this.trSetHeight(null)
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form.scrollHeight)
      })
    },
    initTrHeight2() {
      this.trSetHeight(null)
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form2.scrollHeight)
      })
    },
  },
}
</script>
